<script>
import Vue from 'vue'
import { Toast } from 'vant';
import Swiper from 'swiper'
import {mapGetters,mapActions} from 'vuex'
Vue.use(Toast);
export default {
	name:'Detail',
	computed:mapGetters(['detail']),
	methods:{
		add(detail){
			let arr=[...this.$store.state.buycar];

			let find=false;

			arr.forEach((item,index)=>{
			if(item._id==detail._id){
				item.number++;
				find=true;
			}
			});
			if(!find) {
			detail.number=1;
			arr.push(detail);
			}

			Toast('');
			this.$store.commit('ADD_ITEM',arr);
			localStorage.setItem('buycar_item',JSON.stringify(this.$store.state.buycar))
		}
	},
	created(){
		let id = this.$route.params.id;
		let dataName = this.$route.query.dataName;
		this.$store.dispatch({type:'UPDATE_DETAIL',payload:{id:id,dataName:dataName}})
	},
	updated(){
		new Swiper('.swiper_container_suz1', {
			pagination: {el:'.swiper-pagination',type:'fraction'},
			autoplay:true
		});
	}
}
</script>


